:root{
    --rojo-oscuro: #a11d1c;
    --blanco: #fff;
    --blanco-claro: #f5f6f7;
    --rojo-claro: #e62e2d;
    --rojo-mas-oscuro: #811414;
    --negro: #131313;
    --negro-claro: #2c2c2c;
    --gris-claro: #efeff0;
    --gris: #868485;
    --gris-oscuro: #727172;
    --gris-suave:#ced0d4;
    --plomo-suave: #d8d6d9;
    --verde:#54b209;
    --verde-oscuro: #4ea30a;
    --amarillo: #e4d70c;
    --amarillo-oscuro: #d4c80d;
    --azul-claro: #3f77f3;
    --azul-medio-claro: #3d7ab9;
    --morado: #7024c3;
}

a, p, h1, h2, h3, h4, span{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}


body{
    min-height: 100vh;
}

.contenedor-me-profile{
    display: flex;
    height: 100vh;
