/***********************/
/*parte latera izquierda*/
/***********************/

/* Aplicar un scroll y limitar el alto */
.content-left {
    width: 24%;
    background-color: var(--blanco);
    padding-top: 25px;
    padding-left: 8px;
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    transition: overflow 0.3s ease;
}


/* Estilo del scroll */
.content-left::-webkit-scrollbar {
    width: 6px; /* Ancho del scroll */
}

.content-left::-webkit-scrollbar-thumb {
    background-color: #a6a6a6; 
    border-radius: 10px; 
}

.content-left::-webkit-scrollbar-track {
    background-color: #f0f0f0; 
}

/*PERFIL PRINCIPAL*/

.perfil-principal{
