    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#foto-perfil-principal{
    width: 140px;
    height: 140px;
    border-radius: 50%;
}
.user-profile{
    width: 100%;
    margin-top: 10px;
}
.user-foto {
    position: relative;
    display: inline-block;
}
.user-foto img{
    object-fit:cover;
}

.edit-icon-background {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 30px;
    height: 30px;
    background-color: var(--gris); /* Color plomo */
