/*modal para editar imagen*/
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
  
#modal-edit-img {
    position: relative !important;
    background: #fff !important;
    color: var(--negro);
    padding: 20px;
    padding-bottom: 0px;
    border-radius: 10px;
    text-align: center;
    width: 40% !important;
    cursor: default;
}
  
.close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    color: var(--negro);
    font-size: 16px;
    cursor: pointer;
}

  
.modal-photo {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    margin: 25px auto;
    object-fit: cover;
}

.modal-content h3{
    color: var(--negro);
    font-size: 26px;
    font-weight: 600;
}

.separate-edit-photo{
    width: 100%;
    height: 0.5px;
    background: var(--gris);
    margin-top: 5px;
    margin-bottom: 0px;
}

.modal-actions {
    display: flex;
    justify-content: space-around;
}
  
.action-btn {
    width: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    background-color: #fff;
    border: none;
    padding: 8px 12px;
    color: var(--negro);
    border-radius: 5px;
    cursor: pointer;
    border-radius:5px ;
    padding-top: 15px;
    padding-bottom: 12px;
}
.action-btn span{
    font-size: 15px;
    font-weight: 500;
    letter-spacing: 0.5px;
}

  
.delete-btn {
    background-color: #fff;
}
.action-btn:hover, .delete-btn:hover{
    background:  hsla(0, 1%, 30%, 0.4);
    transition: all 1.5s;
}
label.action-btn{
    margin: 0;
}

.user-separator{
    width: 98%;
    height: 0.2px;
}
.user-separator hr{
    background-color: var(--gris);

}


