/* Contenedor principal que contiene la parte izquierda y derecha */
.contenido-compartido-user {
    display: flex;
}

/* Sección izquierda que define su altura con el contenido */
.compartido-left {
    flex: 1;
}

/* Sección derecha que se ajusta a la altura de la izquierda */
.compartido-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    max-height: 100%; /* Limita su altura al contenido de la izquierda */
}

/* Ajusta la altura máxima de los comentarios */
.sec-comments-user-x {
    max-height: calc(100vh - 200px); /* Ajusta 200px según el espacio que necesitas para otros elementos */
    overflow-y: auto;
    padding: 10px;
    background-color: var(--blanco);
    border-radius: 8px;
}

/* Título de comentarios */
.title-separator-coments {
    position: sticky;
    top: 0;
    background-color: var(--blanco);
    padding: 5px 0;
    z-index: 2;
}

/* Personalización del scroll */
.sec-comments-user-x::-webkit-scrollbar {
    width: 5px;
}

.sec-comments-user-x::-webkit-scrollbar-thumb {
    background-color: #a6a6a6;
    border-radius: 4px;
}

.sec-comments-user-x::-webkit-scrollbar-track {
    background-color: #e0e0e0;
}


/*modal editar usuario*/

#modal-edit-user-general {
    background: white;
    padding: 20px;
    width: 600px;
    max-height: 95vh; /* Limita el alto máximo del modal */
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
}
#modal-edit-user-general h2{
    font-size: 23px;
}
.modal-scroll-content {
    flex-grow: 1;
    overflow-y: auto;
    padding-right: 5px; /* Espacio para el scroll */
    margin-bottom: 20px;
}
.modal-close-button {
    background: none;
    border: none;
    font-size: 20px;
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
}
  
.edit-form-user label {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
    color: var(--negro);
}
  
.edit-form-user input,
.edit-form-user textarea {
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ddd;
    border-radius: 5px;
    resize: none;
}
  
  
.form-buttons {
    display: flex;
    justify-content: space-between;
}

/* Estilo personalizado para el scroll */
.modal-scroll-content::-webkit-scrollbar {
    width: 8px;
}
  
.modal-scroll-content::-webkit-scrollbar-track {
    background: #f1f1f1; /* Color del track */
    border-radius: 10px;
}
  
.modal-scroll-content::-webkit-scrollbar-thumb {
    background-color: #888; /* Color del scroll */
    border-radius: 10px;
    border: 2px solid #f1f1f1; /* Espacio entre el scroll y el track */
}
  
.modal-scroll-content::-webkit-scrollbar-thumb:hover {
    background-color: #555; /* Color al pasar el mouse */
}
  
