
:root{
    --rojo-oscuro: #a11d1c;
    --blanco: #fff;
    --blanco-claro: #f5f6f7;
    --rojo-claro: #e62e2d;
    --rojo-mas-oscuro: #811414;
    --negro: #131313;
    --gris-claro: #efeff0;
    --gris: #868485;
}

a, p, h1, h2, h3, h4, span{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}


body{
    min-height: 100vh;
}

.contenedor-home {
    display: flex;
    height: 100vh;
    padding-top: 60px;
    width: 100%;
}


/***********************/
/*parte latera izquierda*/
/***********************/


.content-left {
    width: 24%; /* no tocar */
    background-color: var(--blanco);
    padding-top: 40px; /* Considera ajustar este valor */
    padding-left: 8px;
    height: 100%; /* Ocupa el 100% de la altura disponible en su contenedor */
    overflow-y: auto; /* Habilita el scroll interno si el contenido excede la altura */
    box-sizing: border-box; /* Incluye padding y border en el cálculo de la altura */
    transition: overflow 0.3s ease;
}

.content-left:hover {
    overflow-y: auto; /* Muestra el scroll al hacer hover */
}

/* Estilo del scroll */
.content-left::-webkit-scrollbar {
    width: 8px;
    background-color: transparent; /* Lo hace invisible fuera de hover */
}

.content-left:hover::-webkit-scrollbar {
    background-color: #f0f0f0; /* Color de fondo visible al hacer hover */
}

.content-left::-webkit-scrollbar-thumb {
    background-color: #a6a6a6;
    border-radius: 10px;
}

.content-left::-webkit-scrollbar-track {
    background-color: #f0f0f0;
}



.user-session{
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;

}

.user-session:hover{
    background-color: var(--gris-claro);
}

.user-session img{
    width: 45px;
    margin-left: 10px;
}
.user-session span{
    color: var(--negro) ;
    font-weight: 500;
    font-size: 16px;
}

.accion-icons{
    display: flex;
    flex-direction: column;
    /* gap: 15px; */
    
}

/*linea separadora*/
.line-separator{
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
}
.linea-ploma{
    width: 100%;
    height: 1px;
    background-color: var(--gris);
}

/*marcas*/

.marcas-registradas{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    gap: 30px;
    margin-bottom: 25px;
}

.marca-empresa img{
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
}


/***********************/
/*parte latera derecha*/
/***********************/

.content-right{
    width: 76%;
}



/*responsivo*/
@media only screen and (max-width: 1199px){
    .content-left{
        display: none;
    }
}

