body{
    min-height: 100vh;
    width: 100%;
}

.contenedor-error-404 {
    display: flex;
    justify-content: center; 
    align-items: center;     
    height: 100vh;         
}

.contenedor-error-404 img{
    text-align: center;
    width: 80%;
    padding-bottom: 20px;
}


@media only screen and (max-width: 575px){
    .contenedor-error-404{
        align-items: baseline !important;
        padding-top: 50px;

    }
}

